import { Component, Prop, h } from "@stencil/core";

type Variant = "primary" | "secondary" | "danger";
type Size = "sm" | "md" | "lg";

@Component({
  tag: "stc-button",
  shadow: false,
  scoped: true,
  styleUrl: "stc-button.css",
})
export class StcButton {
  @Prop() variant: Variant = "primary";
  @Prop() size: Size = "md";
  @Prop({ reflect: true }) disabled = false;
  @Prop() loading = false;

  render() {
    return (
      <button
        class={[
          "stc-button",
          `stc-button--${this.size}`,
          `stc-button--${this.variant}`,
        ].join(" ")}
        disabled={this.disabled || this.loading}
      >
        {this.loading ? <span class="stc-button__spinner" /> : null}
        <slot />
      </button>
    );
  }
}
